
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  margin: 0;
}
.main-box {
  padding: 10px 10px 10px 0;
}
.demo-title1 {
  text-align: left;
}
.demo-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border: 2px solid #A7BFE8;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.demo-box .demo-code, .demo-box .demo-result {
    width: 50%;
}
.demo-box .demo-code {
    border-right: 2px solid #A7BFE8;
}
.demo-box .demo-code pre {
      margin: 0;
      text-align: left;
}
.demo-box .demo-code h3 {
      margin: 0;
      text-align: left;
}
.demo-box .demo-result {
    text-align: left;
    padding: 10px;
}
.demo-box .demo-result h3 {
      margin: 0;
      text-align: left;
}

